@use '../../foundation/mixin' as *;
.u-dis-none {
  display: none;
  &--over-pc{ //PC
    @include breakpoint(pc){
      display: none;
    }
  }
  &--over-tablet{ //tablet
    @include breakpoint(tablet){
      display: none;
    }
  }
  &--sp-and-pc{ //sppc
    display: none;
    @include breakpoint(tablet){
      display: unset;
    }
    @include breakpoint(pc){
      display: none;
    }
  }
}

//important
.u-dis-flex--pc{
  @include breakpoint(pc){
  display: flex!important;
  }
}